			'Moderately elevated blood pressure      If you have been diagnosed with hypertension (elevated blood pressure), it is important to see a doctor regularly and follow the recommendations the doctor gives to control your blood pressure. The scope and intensity of these recommendations depend on the severity (level) and duration of the hypertension and other associated conditions (e.g. other diseases).       The lower your blood pressure, the lower is your risk of stroke (either primary or recurrent stroke). Each 10 mm Hg reduction of systolic blood pressure will reduce your risk of having a stroke by approximately one-third. The usually recommended target for blood pressure reduction is blood pressure below 140/90 mm Hg (in people with diabetes, below 130/80 mm Hg).       As you have moderately high systolic blood pressure (between 140 and 179 mm Hg), you are likely to require regular blood pressure reducing medications, as determined by your doctor. It is important to remember that once blood pressure medication is started it should not be stopped abruptly, as this can cause a rapid increase of blood pressure that can lead to stroke or myocardial infarction.       - Blood pressure lowering medication       There are five main types of blood pressure lowering (antihypertensive) drugs:      Diuretics (e.g. bendrofluazide)      Beta-blockers (e.g. atenolol) and alpha-blockers (e.g. labetalol)      Angiotensin-converting enzyme (ACE) inhibitors (e.g. perindopril, captopril, quinapril, enalapril, ramipril)      Angiotensin 2 receptor blockers (e.g. telmisartan, candesartan)      Calcium channel blockers (e.g. diltiazem, felodipine)      Modern antihypertensive drugs have been proven to be safe and have few side effects. However, if you notice any side effects (such as nausea, headache, dizziness, fatigue, cough etc.), a doctors advice should be sought. If the cause of the symptom(s) is related to blood pressure lowering medication, the doctor may recommend changing the dosage or trying an alternative drug that doesnt cause the same side effects.       - Lifestyle changes       It is important to know that beneficial effects of blood pressure lowering treatment are always enhanced by healthy lifestyle changes, and vice versa.      Losing some weight: On average, every kilogram of weight loss can result in the reduction of systolic and diastolic blood pressure of about 1.1-1.6 mm Hg. see Maintaining healthy weight.      Salt intake reduction: A simple reduction of salt intake by only 2 g a day would result in systolic and diastolic blood pressure reduction of approximately 4 mm Hg systolic and 2 mm Hg diastolic. See &amp;apos;Unhealthy diet&amp;apos;.      Altering the diet - See &amp;apos;Unhealthy diet&amp;apos;.      Increasing exercise level - See &amp;apos;Sedentary lifestyle&amp;apos;.      Stopping smoking or reducing exposure to passive smoking - See &amp;apos;Smoking&amp;apos;.      Two healthy lifestyle modifications are particularly important to reduce blood pressure: salt intake reduction and weight loss.',
